.sas-va-sdk .sas_components-NotificationsBadge-NotificationListItem_item {
  cursor: pointer;
  list-style: none;
  outline: none;
  box-sizing: border-box;
  border: 1px solid transparent;
  display: flex;
  align-items: center;
  min-height: 0.875rem;
  color: var(--list-item-fg);
}
.sas-va-sdk .sas_components-NotificationsBadge-NotificationListItem_item:hover {
  color: var(--list-item-hover-fg);
  background-color: var(--list-item-hover-bg);
}
.sas-va-sdk .sas_components-NotificationsBadge-NotificationListItem_item:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width));
}

.sas-va-sdk .sas_components-NotificationsBadge-NotificationListItem_item-separator {
  border-bottom: 1px solid var(--list-item-separator-bd);
}

.sas-va-sdk .sas_components-NotificationsBadge-NotificationListItem_item-action-icon {
  flex-shrink: 0;
}

.sas-va-sdk .sas_components-NotificationsBadge-NotificationListItem_item-unread {
  font-weight: 600;
  color: var(--color-text-label);
}

.sas-va-sdk .sas_components-NotificationsBadge-NotificationListItem_item-read-notification {
  height: 0.5rem;
  width: 0.5rem;
}

.sas-va-sdk[dir="ltr"] .sas_components-NotificationsBadge-NotificationListItem_item-notification-list-item-content {
  padding-right: 0.5rem;
}

.sas-va-sdk[dir="rtl"] .sas_components-NotificationsBadge-NotificationListItem_item-notification-list-item-content {
  padding-left: 0.5rem;
}

.sas-va-sdk .sas_components-NotificationsBadge-NotificationListItem_item-notification-list-item-content {
  flex: 1 1 auto;
  width: 80%;
}

.sas-va-sdk .sas_components-NotificationsBadge-NotificationListItem_item-notification-list-item-text-title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: pre;
}

.sas-va-sdk .sas_components-NotificationsBadge-NotificationListItem_item-notification-list-item-footer {
  font-size: 0.75rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: pre;
  color: var(--color-text-label);
}

.sas-va-sdk .sas_components-NotificationsBadge-NotificationListItem_item-zero-state {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding-top: 4rem;
}

.sas-va-sdk .sas_components-NotificationsBadge-NotificationListItem_bar {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
}

.sas-va-sdk .sas_components-NotificationsBadge-NotificationListItem_list {
  width: 100%;
  overflow: auto;
}

.sas-va-sdk .sas_components-NotificationsBadge-NotificationListItem_list-container {
  height: 100%;
}

.sas-va-sdk .sas_components-NotificationsBadge-NotificationListItem_counter-label {
  font-weight: 400;
  font-size: 0.875rem;
  text-shadow: none;
}