.sas-va-sdk .sas_components-TextArea-TextArea_container {
  position: relative;
  display: inline-flex;
  font-size: 0.875rem;
}

.sas-va-sdk .sas_components-TextArea-TextArea_textarea {
  width: 100%;
  min-height: 1.5rem;
  padding: var(--textarea-pd);
  border-width: 1px;
  border-style: solid;
  border-color: var(--textarea-bd);
  box-sizing: border-box;
  text-overflow: ellipsis;
  flex-grow: 1;
  background: var(--textarea-bg);
  color: var(--textarea-fg);
  resize: none;
  opacity: var(--textarea-opacity);
  overflow-x: hidden;
}
.sas-va-sdk .sas_components-TextArea-TextArea_fixed-height .sas_components-TextArea-TextArea_textarea {
  height: 100%;
}

.sas-va-sdk .sas_components-TextArea-TextArea_textarea::-webkit-input-placeholder {
  color: var(--textarea-placeholder-fg);
}

.sas-va-sdk .sas_components-TextArea-TextArea_textarea::-moz-placeholder {
  color: var(--textarea-placeholder-fg);
}

.sas-va-sdk .sas_components-TextArea-TextArea_textarea:-ms-input-placeholder {
  color: var(--textarea-placeholder-fg);
}

.sas-va-sdk .sas_components-TextArea-TextArea_textarea::-ms-input-placeholder {
  color: var(--textarea-placeholder-fg);
}

.sas-va-sdk .sas_components-TextArea-TextArea_textarea::-webkit-input-placeholder {
  color: var(--textarea-placeholder-fg);
}

.sas-va-sdk .sas_components-TextArea-TextArea_textarea::-moz-placeholder {
  color: var(--textarea-placeholder-fg);
}

.sas-va-sdk .sas_components-TextArea-TextArea_textarea:-ms-input-placeholder {
  color: var(--textarea-placeholder-fg);
}

.sas-va-sdk .sas_components-TextArea-TextArea_textarea::-ms-input-placeholder {
  color: var(--textarea-placeholder-fg);
}

.sas-va-sdk .sas_components-TextArea-TextArea_textarea::placeholder {
  color: var(--textarea-placeholder-fg);
}

.sas-va-sdk .sas_components-TextArea-TextArea_editable:focus {
  background-color: var(--textarea-focus-bg);
  border-color: var(--textarea-focus-bd);
  color: var(--textarea-focus-fg);
  outline: var(--textarea-editable-focus-outline);
  outline-offset: calc(-1 * var(--focus-width));
}

.sas-va-sdk .sas_components-TextArea-TextArea_read-only {
  padding: 0;
}

.sas-va-sdk .sas_components-TextArea-TextArea_disabled {
  opacity: var(--textarea-disabled-opacity);
  cursor: default;
}

.sas-va-sdk .sas_components-TextArea-TextArea_read-only:focus,
.sas-va-sdk .sas_components-TextArea-TextArea_disabled:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-2px + var(--focus-width) * -1);
}

.sas-va-sdk .sas_components-TextArea-TextArea_message {
  padding: var(--textarea-message-pd);
  font-size: 0.668rem;
  border-width: 1px;
  border-style: solid;
  margin-top: -1px;
  white-space: normal;
  background-color: var(--textarea-message-bg);
  border-color: var(--textarea-message-bd);
  color: var(--textarea-message-fg);
  box-shadow: none;
}

.sas-va-sdk .sas_components-TextArea-TextArea_status-message-container {
  position: absolute;
}