.sas-va-sdk .sas_components-Splitter-Splitter_container_horizontal, .sas-va-sdk .sas_components-Splitter-Splitter_container_vertical {
  display: flex;
}

.sas-va-sdk .sas_components-Splitter-Splitter_container_vertical {
  flex-direction: column;
}

.sas-va-sdk .sas_components-Splitter-Splitter_container_horizontal {
  flex-direction: row;
}

.sas-va-sdk .sas_components-Splitter-Splitter_section {
  overflow: hidden;
}

.sas-va-sdk .sas_components-Splitter-Splitter_bar_vertical, .sas-va-sdk .sas_components-Splitter-Splitter_bar_horizontal {
  background-color: var(--splitter-bg);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sas-va-sdk .sas_components-Splitter-Splitter_bar_vertical:focus, .sas-va-sdk .sas_components-Splitter-Splitter_bar_horizontal:focus {
  outline: var(--focus-light-bg);
}

.sas-va-sdk .sas_components-Splitter-Splitter_bar_horizontal {
  width: 2px;
  cursor: col-resize;
  flex-direction: column;
  align-items: center;
}

.sas-va-sdk .sas_components-Splitter-Splitter_bar_vertical {
  height: 2px;
  cursor: row-resize;
}

.sas-va-sdk .sas_components-Splitter-Splitter_hide-splitter-bar {
  background-color: var(--splitter-bg);
}

.sas-va-sdk .sas_components-Splitter-Splitter_resize-icon {
  pointer-events: none;
}

.sas-va-sdk .sas_components-Splitter-Splitter_collapse-icon_horizontal {
  cursor: pointer;
}
.sas-va-sdk .sas_components-Splitter-Splitter_resize-icon + .sas_components-Splitter-Splitter_collapse-icon_horizontal {
  position: absolute;
  top: calc(50% + 0.5rem);
}

.sas-va-sdk .sas_components-Splitter-Splitter_collapse-icon_vertical {
  cursor: pointer;
}
.sas-va-sdk[dir="ltr"] .sas_components-Splitter-Splitter_resize-icon + .sas_components-Splitter-Splitter_collapse-icon_vertical {
  left: calc(50% + 0.5rem);
}
.sas-va-sdk[dir="rtl"] .sas_components-Splitter-Splitter_resize-icon + .sas_components-Splitter-Splitter_collapse-icon_vertical {
  right: calc(50% + 0.5rem);
}
.sas-va-sdk .sas_components-Splitter-Splitter_resize-icon + .sas_components-Splitter-Splitter_collapse-icon_vertical {
  position: absolute;
}