.sas-va-sdk .sas_components-Link-Link_link {
  cursor: pointer;
  font-size: 0.875rem;
  text-decoration: underline;
  color: var(--link-fg);
  opacity: var(--link-opacity);
}

.sas-va-sdk .sas_components-Link-Link_link:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width));
}

.sas-va-sdk .sas_components-Link-Link_small {
  font-size: 0.75rem;
}

.sas-va-sdk .sas_components-Link-Link_text {
  color: inherit;
  font-size: inherit;
}

.sas-va-sdk .sas_components-Link-Link_nowrap,
.sas-va-sdk .sas_components-Link-Link_nowrap .sas_components-Link-Link_text {
  display: block;
  white-space: pre;
  overflow: hidden;
  text-overflow: ellipsis;
}

.sas-va-sdk .sas_components-Link-Link_disabled {
  cursor: default;
  pointer-events: none;
}

.sas-va-sdk .sas_components-Link-Link_decoration_hover {
  text-decoration: none;
}
.sas-va-sdk .sas_components-Link-Link_decoration_hover:hover {
  text-decoration: underline;
}

.sas-va-sdk .sas_components-Link-Link_decoration_none {
  text-decoration: none;
}