.sas-va-sdk .sas_components-MessageToast-MessageToast_container {
  position: absolute;
  display: inline-flex;
  font-size: 0.875rem;
  align-items: center;
  border-width: 1px;
  border-style: solid;
  border-radius: 1px;
  box-shadow: var(--toast-sh);
  padding: var(--toast-pd);
  z-index: 100;
  margin: 0 auto;
  color: var(--toast-fg);
  background-color: var(--toast-bg);
  border-color: transparent;
}

.sas-va-sdk .sas_components-MessageToast-MessageToast_section-message {
  display: flex;
  align-items: baseline;
  flex-grow: 8;
}

.sas-va-sdk .sas_components-MessageToast-MessageToast_text {
  width: 100%;
}