@font-face {
  font-family: "sas-icons";
  font-weight: normal;
  font-style: normal;
  src: url(assets/fonts/sas-icons-regular.ttf) format("truetype");
}
.sas-va-sdk .sas_components-Icon-Icon_icon {
  display: inline-flex;
  align-items: center;
  speak: none;
  height: 0.875rem;
  width: 0.875rem;
  box-sizing: content-box;
}
.sas-va-sdk .sas_components-Icon-Icon_icon:focus {
  outline: var(--focus-light-bg);
}
.sas-va-sdk .sas_components-Icon-Icon_icon .alert {
  fill: var(--icon-fg, var(--icon-alert-fg));
}
.sas-va-sdk .sas_components-Icon-Icon_icon .warning {
  fill: var(--icon-fg, var(--icon-warning-fg));
}
.sas-va-sdk .sas_components-Icon-Icon_icon .success {
  fill: var(--icon-fg, var(--icon-success-fg));
}
.sas-va-sdk .sas_components-Icon-Icon_icon .information {
  fill: var(--icon-fg, var(--icon-information-fg));
}
.sas-va-sdk .sas_components-Icon-Icon_icon .neutral,
.sas-va-sdk .sas_components-Icon-Icon_icon .iconColor {
  fill: var(--icon-fg, var(--icon-neutral-fg));
}

.sas-va-sdk .sas_components-Icon-Icon_disabled {
  opacity: var(--icon-opacity);
}