.sas-va-sdk .sas_components-SplitButton-SplitButton_container {
  display: flex;
  cursor: pointer;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.sas-va-sdk .sas_components-SplitButton-SplitButton_container:focus {
  outline: var(--button-focus-outline);
  outline-offset: var(--button-focus-outline-offset);
}

.sas-va-sdk[dir="ltr"] .sas_components-SplitButton-SplitButton_button {
  border-right-width: 0;
}

.sas-va-sdk[dir="rtl"] .sas_components-SplitButton-SplitButton_button {
  border-left-width: 0;
}

.sas-va-sdk .sas_components-SplitButton-SplitButton_button {
  flex-grow: 1;
  min-width: 0;
}

.sas-va-sdk .sas_components-SplitButton-SplitButton_menu {
  padding: 0 0.25rem;
}

.sas-va-sdk .sas_components-SplitButton-SplitButton_button.sas_components-SplitButton-SplitButton_active,
.sas-va-sdk .sas_components-SplitButton-SplitButton_menu.sas_components-SplitButton-SplitButton_active {
  color: var(--button-active-fg);
  background-color: var(--button-active-bg);
  border-color: var(--button-active-bd, transparent);
}