.sas-va-sdk .sas_components-ImageSelector-__internal__-BaseImageSelector_image-container {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  padding: 0;
  border: 0;
  max-width: 100%;
  margin-bottom: 1rem;
  position: relative;
  background-color: var(--image-selector-bg);
}

.sas-va-sdk .sas_components-ImageSelector-__internal__-BaseImageSelector_image {
  height: 100%;
  width: 100%;
  max-width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.sas-va-sdk .sas_components-ImageSelector-__internal__-BaseImageSelector_hint-text {
  font-size: 0.75rem;
}

.sas-va-sdk .sas_components-ImageSelector-__internal__-BaseImageSelector_edit-icon {
  position: absolute;
  opacity: 0;
  transition: opacity calc(250ms * var(--nova-animation-duration-factor, 1)) cubic-bezier(0, 0, 0.2, 1) 0ms;
  background: rgba(0, 0, 0, 0.65);
  width: 1.5rem;
  height: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.sas-va-sdk .sas_components-ImageSelector-__internal__-BaseImageSelector_image-container:hover .sas_components-ImageSelector-__internal__-BaseImageSelector_edit-icon,
.sas-va-sdk .sas_components-ImageSelector-__internal__-BaseImageSelector_image-container:focus .sas_components-ImageSelector-__internal__-BaseImageSelector_edit-icon {
  opacity: 1;
}

.sas-va-sdk .sas_components-ImageSelector-__internal__-BaseImageSelector_image-container:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width));
}