.sas-va-sdk .sas_components-ColorPicker-ColorPicker_dropdown-color-swatch {
  border: 1px solid var(--colorpicker-swatch-bd);
  background-color: var(--colorpicker-swatch-bg);
  padding: 0.25rem;
  border-radius: 1px;
  box-sizing: border-box;
  cursor: pointer;
}
.sas-va-sdk .sas_components-ColorPicker-ColorPicker_dropdown-color-swatch:hover {
  border-color: var(--colorpicker-swatch-hover-bd);
  background-color: var(--colorpicker-swatch-hover-bg);
}
.sas-va-sdk .sas_components-ColorPicker-ColorPicker_dropdown-color-swatch:active {
  border-color: var(--colorpicker-swatch-active-bd);
  background-color: var(--colorpicker-swatch-active-bg);
}
.sas-va-sdk .sas_components-ColorPicker-ColorPicker_dropdown-color-swatch.sas_components-ColorPicker-ColorPicker_disabled {
  opacity: var(--colorpicker-swatch-disabled-opacity);
  cursor: default;
}

.sas-va-sdk .sas_components-ColorPicker-ColorPicker_dropdown-color-swatch-inner {
  border: 1px solid var(--colorpicker-swatch-inner-bd);
  width: 0.875rem;
  height: 0.875rem;
  border-radius: 1px;
  box-sizing: border-box;
}

.sas-va-sdk .sas_components-ColorPicker-ColorPicker_no-color-swatch .sas_components-ColorPicker-ColorPicker_dropdown-color-swatch-inner {
  background: linear-gradient(-45deg, transparent 47%, var(--colorpicker-no-color-swatch-mark-bg) 47%, var(--colorpicker-no-color-swatch-mark-bg) 54%, transparent 54%);
}