.sas-va-sdk .sas_components-Label-Label_label {
  display: inline-flex;
  max-width: 100%;
  font-size: 0.875rem;
  color: var(--label-fg);
  --label-text-cursor: text;
}

.sas-va-sdk .sas_components-Label-Label_text-container {
  max-width: 100%;
}

.sas-va-sdk .sas_components-Label-Label_text {
  cursor: var(--label-text-cursor);
  color: inherit;
  white-space: pre-wrap;
  word-break: break-word;
  word-wrap: break-word;
  opacity: var(--label-opacity);
}

.sas-va-sdk[dir="ltr"] .sas_components-Label-Label_icon {
  margin-right: 0.25rem;
}

.sas-va-sdk[dir="rtl"] .sas_components-Label-Label_icon {
  margin-left: 0.25rem;
}

.sas-va-sdk .sas_components-Label-Label_icon {
  align-self: flex-start;
  flex-shrink: 0;
  margin-top: 0.125rem;
  opacity: var(--label-opacity);
}

.sas-va-sdk .sas_components-Label-Label_help {
  min-height: unset;
  vertical-align: text-bottom;
}

.sas-va-sdk .sas_components-Label-Label_nowrap .sas_components-Label-Label_text-container {
  display: inline-flex;
  min-width: 0;
}
.sas-va-sdk .sas_components-Label-Label_nowrap .sas_components-Label-Label_text {
  min-width: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: pre;
  word-break: unset;
  word-wrap: unset;
}

.sas-va-sdk[dir="ltr"] .sas_components-Label-Label_required-mark {
  margin-left: 0.125rem;
}

.sas-va-sdk[dir="rtl"] .sas_components-Label-Label_required-mark {
  margin-right: 0.125rem;
}

.sas-va-sdk .sas_components-Label-Label_required-mark {
  color: var(--label-asterisk-fg);
  opacity: var(--label-opacity);
}