.sas-va-sdk .sas_components-ColorPicker-BasicColorPicker_container {
  padding: var(--color-picker-padding);
  box-sizing: border-box;
}

.sas-va-sdk .sas_components-ColorPicker-BasicColorPicker_standard-color-chips {
  display: grid;
  grid-template-columns: repeat(7, auto);
  grid-template-rows: repeat(5, auto);
  grid-auto-flow: column;
  grid-gap: 0.25rem;
}

.sas-va-sdk .sas_components-ColorPicker-BasicColorPicker_color-block {
  align-items: center;
}