.sas-va-sdk .sas_components-Select-Select_select {
  border: 1px solid var(--select-bd);
  background-color: var(--select-bg);
  color: var(--select-box-fg);
}
.sas-va-sdk .sas_components-Select-Select_select:hover {
  border-color: var(--select-hover-bd);
  background-color: var(--select-hover-bg);
  color: var(--select-hover-fg);
}
.sas-va-sdk .sas_components-Select-Select_select:focus {
  border-color: var(--select-active-bd);
}

.sas-va-sdk .sas_components-Select-Select_select {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 0.5rem;
  min-height: 1.5rem;
  font-size: 0.875rem;
  cursor: pointer;
}

.sas-va-sdk .sas_components-Select-Select_disabled {
  opacity: var(--select-disabled-opacity);
  cursor: default;
}

.sas-va-sdk .sas_components-Select-Select_focus-visible:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width) - 2px);
}

.sas-va-sdk .sas_components-Select-Select_label {
  flex-grow: 1;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  width: var(--longest-text-width);
}

.sas-va-sdk .sas_components-Select-Select_label-disabled {
  cursor: default;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.sas-va-sdk .sas_components-Select-Select_label-placeholder {
  color: var(--select-placeholder-fg);
}

.sas-va-sdk[dir="ltr"] .sas_components-Select-Select_select-icon {
  padding-left: 0.25rem;
}

.sas-va-sdk[dir="rtl"] .sas_components-Select-Select_select-icon {
  padding-right: 0.25rem;
}

.sas-va-sdk .sas_components-Select-Select_select-icon {
  flex-shrink: 0;
}

.sas-va-sdk .sas_components-Select-Select_dropdown-popover {
  white-space: nowrap;
}

.sas-va-sdk .sas_components-Select-Select_dropdown-popover-item {
  cursor: default;
}

.sas-va-sdk .sas_components-Select-Select_disabled {
  opacity: 0.3;
  cursor: default;
}

.sas-va-sdk .sas_components-Select-Select_status {
  border-color: var(--select-status-bd);
  color: var(--select-status-fg);
}
.sas-va-sdk .sas_components-Select-Select_status:hover {
  border-color: var(--select-status-bd);
  color: var(--select-status-fg);
}
.sas-va-sdk .sas_components-Select-Select_status:focus {
  border-color: var(--select-status-bd);
  color: var(--select-status-fg);
}

.sas-va-sdk .sas_components-Select-Select_status-icon {
  flex-shrink: 0;
  top: 1px;
  bottom: 1px;
  display: flex;
  align-items: center;
  margin-left: 0.5rem;
  width: 0.875rem;
}