.sas-va-sdk .sas_components-Avatar-Avatar_noselect, .sas-va-sdk .sas_components-Avatar-Avatar_inner-container {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently supported by Chrome, Opera and Firefox */
}

.sas-va-sdk .sas_components-Avatar-Avatar_avatar {
  color: var(--avatar-fg);
  border-color: var(--avatar-bd);
}
.sas-va-sdk .sas_components-Avatar-Avatar_avatar svg > .iconColor {
  fill: var(--avatar-fg);
}

.sas-va-sdk .sas_components-Avatar-Avatar_inner-container {
  background-color: var(--avatar-bg);
  align-items: center;
  border-radius: 50%;
  border-style: solid;
  border-width: 1px;
  box-sizing: border-box;
  display: flex;
  height: inherit;
  justify-content: center;
  width: inherit;
}

.sas-va-sdk .sas_components-Avatar-Avatar_has-image {
  border-style: none;
  border: none;
}

.sas-va-sdk .sas_components-Avatar-Avatar_avatar > * {
  font-weight: 600;
  text-transform: uppercase;
}

.sas-va-sdk .sas_components-Avatar-Avatar_avatar-image {
  border-radius: 50%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%;
}
.sas-va-sdk .sas_components-Avatar-Avatar_avatar-image.sas_components-Avatar-Avatar_image-invisible {
  visibility: hidden;
}

.sas-va-sdk .sas_components-Avatar-Avatar_avatar-button {
  cursor: pointer;
}

.sas-va-sdk .sas_components-Avatar-Avatar_avatar-button:hover {
  background-color: var(--avatar-hover-bg);
  color: var(--avatar-hover-fg);
}
.sas-va-sdk .sas_components-Avatar-Avatar_avatar-button:hover svg > .iconColor {
  fill: var(--avatar-hover-fg);
}

.sas-va-sdk .sas_components-Avatar-Avatar_avatar-button:active,
.sas-va-sdk .sas_components-Avatar-Avatar_avatar-button.sas_components-Avatar-Avatar_active {
  background-color: var(--avatar-active-bg);
  color: var(--avatar-active-fg);
}
.sas-va-sdk .sas_components-Avatar-Avatar_avatar-button:active svg > .iconColor,
.sas-va-sdk .sas_components-Avatar-Avatar_avatar-button.sas_components-Avatar-Avatar_active svg > .iconColor {
  fill: var(--avatar-active-fg);
}

.sas-va-sdk .sas_components-Avatar-Avatar_small {
  font-size: 0.875rem;
  height: 1.375rem;
  width: 1.375rem;
}

.sas-va-sdk .sas_components-Avatar-Avatar_medium {
  font-size: 1.125rem;
  height: 2rem;
  width: 2rem;
}

.sas-va-sdk .sas_components-Avatar-Avatar_extra-large {
  font-size: 3rem;
  height: 5.5rem;
  width: 5.5rem;
}

.sas-va-sdk .sas_components-Avatar-Avatar_small .sas_components-Avatar-Avatar_avatar-icon {
  width: 0.875rem;
  height: 0.875rem;
}

.sas-va-sdk .sas_components-Avatar-Avatar_medium .sas_components-Avatar-Avatar_avatar-icon {
  width: 1.125rem;
  height: 1.125rem;
}

.sas-va-sdk .sas_components-Avatar-Avatar_extra-large .sas_components-Avatar-Avatar_avatar-icon {
  width: 3rem;
  height: 3rem;
}

.sas-va-sdk .sas_components-Avatar-Avatar_avatar:focus {
  outline: var(--avatar-focus);
  outline-offset: calc(-1 * var(--focus-width) - 1px);
}