.sas-va-sdk .sas_components-BadgeButton-BadgeButton_text {
  position: relative;
}

.sas-va-sdk[dir="ltr"] .sas_components-BadgeButton-BadgeButton_with-text-icon .sas_components-BadgeButton-BadgeButton_text {
  margin-left: var(--badge-button-text-mg);
}

.sas-va-sdk[dir="rtl"] .sas_components-BadgeButton-BadgeButton_with-text-icon .sas_components-BadgeButton-BadgeButton_text {
  margin-right: var(--badge-button-text-mg);
}

.sas-va-sdk .sas_components-BadgeButton-BadgeButton_basic {
  display: flex;
  align-items: center;
  justify-content: center;
}
.sas-va-sdk .sas_components-BadgeButton-BadgeButton_basic:not(.sas_components-BadgeButton-BadgeButton_square) .sas_components-BadgeButton-BadgeButton_text::before {
  content: "(";
  position: relative;
  top: -1px;
}
.sas-va-sdk .sas_components-BadgeButton-BadgeButton_basic:not(.sas_components-BadgeButton-BadgeButton_square) .sas_components-BadgeButton-BadgeButton_text::after {
  content: ")";
  position: relative;
  top: -1px;
}

.sas-va-sdk .sas_components-BadgeButton-BadgeButton_square {
  background-color: var(--badge-button-bg);
  color: var(--badge-button-fg);
  border-radius: 1px;
}
.sas-va-sdk .sas_components-BadgeButton-BadgeButton_square .sas_components-BadgeButton-BadgeButton_text {
  color: var(--badge-button-fg);
  font-size: 0.75rem;
  font-weight: 600;
  padding: 0 var(--badge-button-text-pd);
}