.sas-va-sdk .sas_components-Dialog-Dialog_dialog {
  color: var(--dialog-content-fg);
  background-color: var(--dialog-content-bg);
  border: var(--dialog-content-bd);
  box-shadow: var(--dialog-content-sh);
  margin: auto;
  border-radius: 1px;
  min-width: 25rem;
  min-height: 12.5rem;
  max-width: calc(100% - 4rem);
  max-height: calc(100% - 4rem);
  display: flex;
  flex-flow: column nowrap;
  position: absolute;
  box-sizing: border-box;
}

.sas-va-sdk .sas_components-Dialog-Dialog_dialog-header {
  flex-shrink: 0;
  color: var(--dialog-header-fg);
  background-color: var(--dialog-header-bg);
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 2rem;
  padding-bottom: 0.5rem;
}

.sas-va-sdk .sas_components-Dialog-Dialog_dialog-title {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--dialog-header-fg);
  overflow: hidden;
  text-align: center;
}

.sas-va-sdk .sas_components-Dialog-Dialog_dialog-content {
  flex-grow: 1;
  flex-basis: 100%;
  padding: var(--dialog-content-pd);
  max-width: 100%;
  box-sizing: border-box;
  overflow: auto;
}

.sas-va-sdk .sas_components-Dialog-Dialog_dialog-footer {
  flex-shrink: 0;
  margin: 0 1rem 1rem 1rem;
  display: flex;
  justify-content: flex-end;
}

.sas-va-sdk .sas_components-Dialog-Dialog_dialog-footer-controls {
  flex-grow: 1;
}

.sas-va-sdk[dir="ltr"] .sas_components-Dialog-Dialog_dialog-button {
  margin-left: 0.5rem;
}

.sas-va-sdk[dir="rtl"] .sas_components-Dialog-Dialog_dialog-button {
  margin-right: 0.5rem;
}

.sas-va-sdk[dir="ltr"] .sas_components-Dialog-Dialog_close-icon {
  right: 0.25rem;
}

.sas-va-sdk[dir="rtl"] .sas_components-Dialog-Dialog_close-icon {
  left: 0.25rem;
}

.sas-va-sdk .sas_components-Dialog-Dialog_close-icon {
  position: absolute;
  top: 0.25rem;
  cursor: pointer;
}

.sas-va-sdk .sas_components-Dialog-Dialog_drag-cursor {
  cursor: move;
}

.sas-va-sdk .sas_components-Dialog-Dialog_no-drag-cursor {
  cursor: default;
}

.sas-va-sdk[dir="ltr"] .sas_components-Dialog-Dialog_drag-icon-wrapper {
  left: 0;
}

.sas-va-sdk[dir="rtl"] .sas_components-Dialog-Dialog_drag-icon-wrapper {
  right: 0;
}

.sas-va-sdk .sas_components-Dialog-Dialog_drag-icon-wrapper {
  position: absolute;
  top: 0;
  pointer-events: none;
}
.sas-va-sdk .sas_components-Dialog-Dialog_drag-icon-wrapper:focus {
  outline: var(--dialog-header-drag-icon-focus);
  outline-offset: calc(-1 * var(--focus-width) - 2px);
}

.sas-va-sdk .sas_components-Dialog-Dialog_drag-icon {
  cursor: move;
}

.sas-va-sdk[dir="ltr"] .sas_components-Dialog-Dialog_grip-icon-wrapper {
  right: 0;
}

.sas-va-sdk[dir="rtl"] .sas_components-Dialog-Dialog_grip-icon-wrapper {
  left: 0;
}

.sas-va-sdk .sas_components-Dialog-Dialog_grip-icon-wrapper {
  cursor: se-resize;
  width: 1rem;
  height: 1rem;
  position: absolute;
  bottom: 0;
}
.sas-va-sdk .sas_components-Dialog-Dialog_grip-icon-wrapper:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width) - 2px);
}

.sas-va-sdk .sas_components-Dialog-Dialog_grip-cursor-rtl {
  cursor: sw-resize;
}

.sas-va-sdk .sas_components-Dialog-Dialog_icon-container {
  width: 1.5rem;
  height: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: var(--dialog-header-accent);
}

.sas-va-sdk .sas_components-Dialog-Dialog_accent {
  width: 100%;
  height: 0.125rem;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  z-index: 1;
  background-color: var(--dialog-header-accent);
}

.sas-va-sdk .sas_components-Dialog-Dialog_header-image {
  margin-top: -2rem;
}

.sas-va-sdk[dir="ltr"] .sas_components-Dialog-Dialog_header-logo {
  left: 1rem;
}

.sas-va-sdk[dir="rtl"] .sas_components-Dialog-Dialog_header-logo {
  right: 1rem;
}

.sas-va-sdk .sas_components-Dialog-Dialog_header-logo {
  position: absolute;
  top: 1rem;
}