.sas_ReportContainer-internal-SlideTransition_container {
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: 1;
  animation-play-state: paused;
  flex-grow: 1;
  display: flex;
  flex-basis: 0;
  overflow: hidden;
}

.sas_ReportContainer-internal-SlideTransition_base {
  overflow: hidden;
  flex-grow: 1;
  display: flex;
  flex-shrink: 1;
  flex-direction: column;
}

.sas_ReportContainer-internal-SlideTransition_container-start_left {
  width: 200%;
  transform: translate3d(-50%, 0, 0);
  pointer-events: none;
}

.sas_ReportContainer-internal-SlideTransition_container-start_right {
  width: 200%;
  transform: translate3d(0, 0, 0);
  pointer-events: none;
}

.sas_ReportContainer-internal-SlideTransition_container-final_left {
  transform: translate3d(0, 0, 0);
}

.sas_ReportContainer-internal-SlideTransition_container-final_right {
  transform: translate3d(-50%, 0, 0);
}

.sas_ReportContainer-internal-SlideTransition_container-animate_left {
  animation-name: sas_ReportContainer-internal-SlideTransition_container-animate_left;
  animation-play-state: running;
}

@keyframes sas_ReportContainer-internal-SlideTransition_container-animate_left {
  from {
    transform: translate3d(-50%, 0, 0);
  }
  to {
    transform: translate3d(0x, 0, 0);
  }
}
.sas_ReportContainer-internal-SlideTransition_container-animate_right {
  animation-name: sas_ReportContainer-internal-SlideTransition_container-animate_right;
  animation-play-state: running;
}

@keyframes sas_ReportContainer-internal-SlideTransition_container-animate_right {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    transform: translate3d(-50%, 0, 0);
  }
}
