.sas-va-sdk .sas_components-Token-Token_token {
  display: inline-flex;
  align-items: center;
  box-sizing: border-box;
  height: 1.375rem;
  padding: 0.25rem 0.5rem;
  border-style: solid;
  border-width: 1px;
  border-radius: 0.668rem;
  font-size: 0.75rem;
  line-height: 1.125rem;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: var(--token-bg);
  border-color: var(--token-bd);
  color: var(--token-fg);
}

.sas-va-sdk .sas_components-Token-Token_dropdown {
  cursor: pointer;
}

.sas-va-sdk[dir="ltr"] .sas_components-Token-Token_token-name {
  margin-left: 0.05rem;
}

.sas-va-sdk[dir="rtl"] .sas_components-Token-Token_token-name {
  margin-right: 0.05rem;
}

.sas-va-sdk .sas_components-Token-Token_token-name {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.sas-va-sdk[dir="ltr"] .sas_components-Token-Token_token-close {
  margin-left: 0.5rem;
}

.sas-va-sdk[dir="rtl"] .sas_components-Token-Token_token-close {
  margin-right: 0.5rem;
}

.sas-va-sdk[dir="ltr"] .sas_components-Token-Token_token-close {
  margin-right: -0.25rem;
}

.sas-va-sdk[dir="rtl"] .sas_components-Token-Token_token-close {
  margin-left: -0.25rem;
}

.sas-va-sdk .sas_components-Token-Token_token-close {
  cursor: pointer;
  display: flex;
}

.sas-va-sdk .sas_components-Token-Token_token-close-icon {
  height: 0.75rem;
  width: 0.75rem;
}

.sas-va-sdk .sas_components-Token-Token_focus-visible:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-2px + calc(-1 * var(--focus-width)));
}